.home{
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;

}

.home_row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}
.home_image {
    display: flex;
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: -webkit-gradient( linear,right 0%,left bottom,from(rgba(0,0,0,1)) , to( rgba(0,0,0,0)));
}