.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: white;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header_logo{
    width: 60px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 5px;
}

.header_search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;

}
.header_input{
    height: 12px;
    padding: 10px;
    border: 1px solid pink;
    width: 100%;
}



.header_searchIcon{
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
}

.header_option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: pink;
    text-decoration:none;
}

.header_nav{
    display: flex;
    justify-content: space-evenly;
}

.header_optionLineOne{
    font-size: 10px;
}
.header_optionLineTwo{
    font-size: 13px;
    font-weight: 800;
}
.header_optionBasket{
    display: flex;
    align-items: center;
    color: pink;
}


.header_basketCount{
    margin-left: 10px;
    margin-right: 10px;
}